/* chat1.css */
* {
  list-style: none;
  margin: 0px;
  padding: 0px;
}

.chat1 .chat-container {
  width: 100%;
  max-width: 400px;
  height: 400px;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  overflow-y: scroll;
}

.chat1 #form {
  max-width: 400px;
  height: 400px;
}

.chat1 .chat-messages {
  max-height: 300px;
  overflow-y: auto;
  padding: 10px;
  margin-bottom: 10px;
}

.chat1 .chat-message {
  background-color: #007bff;
  color: #fff;
  padding: 3px 5px;
  border-radius: 20px;
  margin-bottom: 3px;
  max-width: 80%;
}

.chat1 .chat-input {
  max-width: 200px;
  padding: 10px 15px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 20px;
  margin: 10px;
}

.chat1 .chat-input:focus {
  outline: none;
  border-color: #007bff;
}

.chat1 .chat-button {
  padding: 10px 20px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 20px;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
}

.chat1 .chat-button:hover {
  background-color: #0056b3;
}

.chat1 .chat-input-codigo{
  padding: 10px 15px;
  font-size: 14px;
  border: #0056b3;
  border: 1px solid #ccc;
  border-radius: 20px;
  background-color: #d6e2ee;
  margin: 10px;
  color:blueviolet;
}
.chat1 .chat-input-codigo:hover{
  color: rgb(238, 238, 11);
}

/* Estilos del scrollbar */
.chat1 .chat-messages::-webkit-scrollbar {
  width: 5px; /* Ancho del scrollbar */
}

.chat1 .chat-messages::-webkit-scrollbar-track {
  background: transparent; /* Color del fondo del scrollbar */
}

.chat1 .chat-messages::-webkit-scrollbar-thumb {
  background-color: #ccc; /* Color del scrollbar */
  border-radius: 10px; /* Borde del scrollbar */
}
