/* chat2.css */
* {
  list-style: none;
  margin: 0px;
  padding: 0px;
  background-color: transparent; /* Ajustado el color de fondo a transparent */
}

.chat2 .chat-container {
  width: 100%;
  max-width: 400px;
  height: 400px;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 5 5px 5px rgba(0, 0, 0, 0.1);
  overflow-y: scroll;
}

.chat2 #form {
  max-width: 400px;
  height: 400px;
}

.chat2 .chat-messages {
  max-height: 300px;
  overflow-y: auto;
  padding: 10px;
  margin-bottom: 10px;
}

.chat2 .chat-message {
  color: transparent;
  padding: 3px 5px;
  border-radius: 20px;
  margin-bottom: 3px;
  max-width: 80%;
}
.chat2 .chat-message:hover {
  color: #ccc;
}

.chat2 .chat-input {
  width: calc(100% - 20px);
  padding: 10px 15px;
  font-size: 14px;
  border: transparent;
  border-radius: 20px;
  margin: 10px;
  color: #e9e9e9;
}
.chat2 .chat-input-codigo {
  outline: none;
  padding: 10px 15px;
  font-size: 14px;
  border: transparent;
  border-radius: 20px;
  margin: 10px;
  color: transparent;
}
.chat2 .chat-input-codigo:hover {
  outline: none;
  color: beige;
}

.chat2 .chat-input:focus {
  outline: none;
  border-color: #007bff;
}

.chat2 .chat-button {
  display: none;
  padding: 10px 20px;
  font-size: 14px;
  border: none;
  border-radius: 20px;
  background-color: #1b76d6;
  color: #fff;
  cursor: pointer;
}

.chat2 .chat-button:hover {
  background-color: #0056b3;
}
/* Estilos del scrollbar */
.chat2 .chat-messages::-webkit-scrollbar {
  width: 5px; /* Ancho del scrollbar */
}

.chat2 .chat-messages::-webkit-scrollbar-track {
  background: transparent; /* Color del fondo del scrollbar */
}

.chat2 .chat-messages::-webkit-scrollbar-thumb {
  background-color: #ccc; /* Color del scrollbar */
  border-radius: 10px; /* Borde del scrollbar */
}
